<template>
  <div style="max-width: 100%;box-sizing: border-box" class="askQuestions">
    <myHeader :isBack="true" backPath="/personInfo" title="我要提问"></myHeader>
    <div class="seat-head"></div>
    <div class="operate-block">
      <div class="tip-title">问题描述</div>
      <div class="edit-block">
        <mt-field placeholder="请输入您的问题" type="textarea" v-model="content"></mt-field>
      </div>
    </div>
    <SubmitBtn @click.native.prevent="submitData"></SubmitBtn>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import SubmitBtn from "../../../../components/SubmitBtn";
  import  {askQuestions} from  "@/api/feedback"
  import { Toast } from 'mint-ui';
  export default {
    name: 'askQuestions',
    data() {
      return {
        content:""
      }
    },
    components: {
      SubmitBtn,
      myHeader
    },
    methods: {
      submitData(){
        if(this.content !=""){
          askQuestions(this.content).then((res)=>{
            Toast({
              message: '提交成功',
              duration: 1000
            });
           setTimeout(()=>{
             this.$router.push('/problemFeedback')
           },2000)
          }).catch((err)=>{
            Toast({
              message: err,
              duration: 1000
            });
          })
        }else{
          Toast({
            message: '输入的问题不能为空！',
            duration: 1000
          });
        }

      }
    }
  }
</script>

<style lang="scss">
  .askQuestions {
    min-height: 100vh;
    background-color: #F8F8F8;

    .head {
      background-color: #FFF;
    }
    .mint-cell-wrapper{
        padding-left:.25rem;
       padding-top:.1rem;
    }
    .operate-block {
      margin-top: .1rem;
      background: #fff;
      .tip-title {
        margin-left: .25rem;
        height: .45rem;
        line-height: .45rem;
        font-size: .16rem;
        border-bottom: 1px solid #dddddd;
      }
      .edit-block {
        .mint-field-core {
          height: 1.36rem;
        }
        .mint-cell {
          border: 0;
        }
      }
    }

  }
</style>



